.ayo-blog-box {
    position: relative;
    z-index: 1;
    margin-bottom: 24px;
    @include media-only ("md", min) {
        margin-bottom: 0px;
    }
    &:hover {
        .item-img {
            a {
                img {
                    transform: scale(1.2);
                    filter: grayscale(100%);
                }
            }
        }
    }
    .item-img {
        position: relative;
        border-radius: 6px 6px 0px 0px;
        overflow: hidden;
        a {
            width: 100%;
            overflow: hidden;
            border-radius: var(--border-radius-lg);
            img {
                border-radius: var(--border-radius-lg);
                transition: all .6s ease-out;
                width: 100%;
                filter: grayscale(0);
            }
        }
    }
    .item-content {
        padding-top: 45px;
        position: relative;
        .tag {
            a {
                background: var(--color-primary);
                border-radius: var(--border-radius-lg);
                padding: 15px 20px;
                font-weight: 700;
                font-size: 15px;
                line-height: 20px;
                color: var(--color-white);
                display: inline-block;
                text-align: center;
                position: absolute;
                top: -30px;
                left: 55px;
                transition: all .4s ease-in-out;
                &:hover {
                    background-color: var(--color-white);
                    color: var(--color-primary);
                }
            }
        }
        .entry-meta {
            display: flex;
            gap: 50px;
            li {
                color: #717171;
                font-size: 16px;
                @include media-only ("xs", min) {
                    font-size: 18px;
                }
                i {
                    color: #717171;
                    margin-right: 10px;
                }
                a {
                    color: #717171;
                    transition: all .4s ease-out;
                    font-size: 16px;
                    @include media-only ("xs", min) {
                        font-size: 18px;
                    }
                    &:hover {
                        color: var(--color-primary);
                    }
                    i {
                        color: #717171;
                        margin-right: 10px;
                    }
                }
            }
        }
        .title {
            margin-bottom: 13px;
            a {
                font-size: 20px;
                color: var(--color-black);
                transition: all .4s ease-in-out;
                @include media-only ("xs", min) {
                    font-size: 26px;
                }
                &:hover {
                    color: var(--color-primary);
                }
            }
        }
    }
}

.ayo-blog-box.style-2 {
    &:hover {
        .item-img {
            a {
                img {
                    transform: scale(1.2);
                }
            }
        }
    }
    .item-img {
        a {
            img {
               
                filter: grayscale(0);
            }
        }
    }
}


.ayo-blog-box-2 {
    margin-bottom: 24px;
    @include media-only ("lg", min) {
        margin-bottom: 0px;
    }
    &:hover {
        .item-img {
            a {
                img {
                    transform: scale(1.2);
                }
            }
        }
    }
    .item-img {
        position: relative;
        overflow: hidden;
        border-radius: 20px 20px 0px 0px;
        a {
            width: 100%;
            border-radius: 20px 20px 0px 0px;
            img {
                width: 100%;
                border-radius: 20px 20px 0px 0px;
                transform: scale(1);
                transition: all .8s ease-out;
            }
        }
        .tag-wrap {
            .tag {
                background: var(--color-primary);
                border-radius: 55px;
                padding: 10px 20px;
                font-weight: 700;
                font-size: 15px;
                line-height: 20px;
                color: var(--color-white);
                display: inline-block;
                text-align: center;
                position: absolute;
                top: 30px;
                left: 30px;
                transition: all .4s ease-in-out;
                width: auto;
                &:hover {
                    background-color: var(--color-white);
                    color: var(--color-primary);
                }
            }
        }
    }
    .item-content {
        background: #FFFFFF;
        box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.05);
        border-radius: 0px 0px 20px 20px;
        padding: 23px;
        @include media-only ("xs", min) {
            padding: 23px 30px 43px;
        }
        .entry-meta {
            display: flex;
            gap: 30px;
            margin-bottom: 5px;
            @include media-only (375px, max) {
                gap: 25px;
            }
            li {
                color: #717171;
                font-size: 16px;
                @include media-only ("md", min) {
                    font-size: 18px;
                }
                i {
                    color: #717171;
                    margin-right: 10px;
                }
                a {
                    color: #717171;
                    transition: all .4s ease-out;
                    font-size: 16px;
                    @include media-only ("md", min) {
                        font-size: 18px;
                    }
                    &:hover {
                        color: var(--color-primary);
                    }
                    i {
                        color: #717171;
                        margin-right: 10px;
                    }
                }
            }
        }
        .title {
            margin-bottom: 0;
            @include media-only ("xs", min) {
                line-height: 40px;
            }
            a {
                font-weight: 500;
                font-size: 20px;
                color: $color-black;
                transition: all .4s ease-in-out;
                @include media-only ("xs", min) {
                    font-size: 24px;
                }
                @include media-only ("md", min) {
                    font-size: 26px;
                }
                &:hover {
                    color: $color-primary;
                }
            }
        }
    }
}
