.ayo-blog-box-2.style-2 {
    margin-bottom: 24px;
    position: relative;
    z-index: 1;
}

.ayo-blog-box-2.style-3 {
    margin-bottom: 24px;
    position: relative;
    z-index: 1;
    .play-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        text-align: center;
        .play-icon {
            font-size: 24px;
            color: $color-primary;
            height: 100px;
            width: 100px;
            background: $color-white;
            border: 0;
        }
    }
    .item-content {
        .title {
            font-weight: 500;
            font-size: 20px;
            margin-bottom: 7px;
            @include media-only ("sm", min) {
                font-size: 30px;
                line-height: 45px;
            }
        }
        p {
            margin-bottom: 30px;
        }
        .read-button {
            a {
                background: #F7F7F7;
                border-radius: 5px;
                font-weight: 500;
                font-size: 18px;
                line-height: 23px;
                display: inline-block;
                color: $color-black;
                padding: 12px 24px;
                transition: all .4s ease-in-out;
                &:hover {
                    color: $color-white;
                    background: $color-primary;
                }
            }
        }
    }
}

.single-blog-box-1 {
    position: relative;
    z-index: 1;
    .top-part {
        .entry-meta {
            display: flex;
            gap: 30px;
            margin-bottom: 12px;
            li {
                color: #717171;
                font-size: 18px;
                i {
                    color: #717171;
                    margin-right: 10px;
                }
                a {
                    color: #717171;
                    transition: all .4s ease-out;
                    font-size: 18px;
                    &:hover {
                        color: var(--color-primary);
                    }
                    i {
                        color: #717171;
                        margin-right: 10px;
                    }
                }
            }
        }
        .title {
            font-weight: 600;
            font-size: 20px;
            line-height: 35px;
            margin-bottom: 25px;
            @include media-only ("xs", min) {
                font-size: 30px;
                line-height: 45px;
            }
            @include media-only ("lg", min) {
                font-size: 40px;
                line-height: 55px;
            }
        }
        p {
            margin-bottom: 35px;
        }
        .item-img {
            margin-bottom: 35px;
            img {
                border-radius: 20px;
                width: 100%;
            }
        }
        .description {
            margin-bottom: 30px;
        }
    }
    .action-area {
        padding: 10px 0px 50px;
        .title {
            font-weight: 600;
            font-size: 19px;
            line-height: 28px;
            margin-bottom: 12px;
            @include media-only ("xs", min) {
                font-size: 22px;
            }
        }
        p {
            margin-bottom: 30px;
        }
        .about-img {
            margin-bottom: 45px;
            .item-img {
                img {
                    border-radius: 20px;
                    width: 100%;
                }
            }
        }
        .description-3 {
            margin-bottom: 25px;
        }
        .description-4 {
            margin-bottom: 15px;
        }
        .item-quote {
            font-weight: 500;
            font-size: 20px;
            line-height: 40px;
            font-family: $font-title;
            color: $color-black;
            border: 1px solid #D9D9D9;
            border-radius: 20px;
            padding: 35px;
            margin-bottom: 40px;
            @include media-only ("xs", min) {
                padding: 35px 55px;
                font-size: 22px;
            }
        }
        .description-6 {
            margin-bottom: 40px;
        }
        .single-blog-tag-share {
            display: block;
            margin-bottom: 60px;
            @include media-only ("sm", min) {
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
            .single-tag-area {
                margin-bottom: 15px;
                @include media-only ("sm", min) {
                    margin-bottom: 0px;
                }
                ul {
                    display: flex;
                    gap: 8px;
                    align-items: center;
                    flex-wrap: wrap;
                    @include media-only ("xs", min) {
                        flex-wrap: nowrap;
                    }
                    li {
                        a {
                            font-weight: 500;
                            font-size: 15px;
                            line-height: 28px;
                            border: 1px solid #d9d9d9;
                            background: transparent;
                            border-radius: 30px;
                            padding: 5px 16px;
                            display: inline-block;
                            overflow: hidden;
                            position: relative;
                            z-index: 1;
                            transition: all .4s ease-in-out;
                            color: $color-black;
                            &:hover {
                                color: $color-white;
                                background: $color-primary;
                                border-color: $color-primary;
                            }
                        }
                    }
                    .tag {
                        font-weight: 600;
                        font-size: 18px;
                        line-height: 28px;
                        color: var(--color-black);
                        position: relative;
                        font-family: $font-title;
                        top: -5px;
                    }
                }
            }
            .single-share-area {
                ul {
                    display: flex;
                    align-items: center;
                    gap: 15px;
                    li {
                        a {
                            &:hover {
                                i {
                                    color: var(--color-primary);
                                }
                            }
                            i {
                                color: #8D8D8D;
                                font-size: 15px;
                                transition: all .3s ease-in-out;
                            }
                        }
                    }
                    .share {
                        font-weight: 500;
                        font-size: 15px;
                        line-height: 28px;
                        color: var(--color-black);
                        font-family: $font-title;
                    }
                }
            }
        }
        .post-paginaton {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .navigation {
                a {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 21px;
                    color: $color-black;
                    transition: all .4s ease-in-out;
                    &:hover {
                        color: $color-primary;
                        i {
                            color: $color-primary;
                        }
                    }
                    i {
                        color: $color-black;
                        transition: all .4s ease-in-out;
                    }
                }

            }
            .old-post {
                a {
                    i {
                        margin-right: 8px;
                    }
                }
            }
            .new-post {
                a {
                    i {
                        margin-left: 8px;
                    }
                }
            }
        }
    }
}

.single-blog-bottom-wrap {
    background: $color-white;
    padding: 70px 0px 65px;
}

.single-blog-bottom-area {
    .single-comment-post {
        padding: 0px 0px 30px 0px;
        .title {
            font-weight: 700;
            font-size: 19px;
            line-height: 28px;
            margin-bottom: 25px;
            text-align: center;
            @include media-only ("xs", min) {
                font-size: 22px;
                text-align: left;
            }
        }
        .comment-post {
            display: block;
            position: relative;
            margin-bottom: 40px;
            @include media-only ("xs", min) {
                display: flex;
                gap: 18px;
            }
            &:nth-child(odd) {
                padding-left: 0px;
                @include media-only ("xs", min) {
                    padding-left: 40px;
                }
            }
            .item-img {
                text-align: center;
                img {
                    height: 60px;
                    width: 60px;
                    border-radius: 50%;
                }
                @include media-only ("xs", min) {
                    text-align: left;
                }
            }
            .content {
                text-align: center;
                margin-top: 20px;
                @include media-only ("xs", min) {
                    flex: 1;
                    margin-top: 0;
                }
                .title {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 22px;
                    margin-bottom: 2px;
                }
                .date {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 28px;
                    margin-bottom: 15px;
                }
                p {
                    @include media-only ("xs", min) {
                        margin-bottom: 0px;
                    }
                }
            }
            .replay-btn {
                @include media-only ("xs", max) {
                    text-align: center;
                }
                a {
                    font-size: 16px;
                    line-height: 30px;
                    display: inline-block;
                    transition: all .3s ease-in-out;
                    &:hover {
                        svg {
                            transform: translateX(2px);
                        }
                    }
                    svg {
                        color: $color-primary;
                        transition: all .3s ease-in-out;
                    }
                }
            }
        }
    }
    .single-contact-post {
        .contact-form-style-1 {
            .title {
                font-weight: 700;
                font-size: 19px;
                line-height: 28px;
                @include media-only ("xs", min) {
                    font-size: 22px;
                }
            }
            .label {
                font-size: 15px;
                line-height: 20px;
                margin-bottom: 35px;
                display: block;
            }
            .form-group {
                margin-bottom: 15px;
                .rt-form-control {
                    background: #f9f9f9;
                    border: 0;
                    border-radius: 10px;
                    &::placeholder {
                        color: #7B7B7B;
                    }
                }
                .rt-form-control.rt-textarea {
                    min-height: 240px;
                }

                .form-check {
                    padding: 0;
                    .form-check-input {
                        display: none;
                        &:focus {
                            outline: 0;
                            box-shadow: none;
                        }
                    }
                    input[type=checkbox] + label {
                        padding: 0;
                        font-size: 15px;
                        line-height: 28px;
                        text-transform: capitalize;
                        color: $color-body;
                    }
                    input[type=checkbox] + label:before {
                        content: "\2714";
                        border: 1px solid $color-body;
                        border-radius: 0px;
                        display: inline-block;
                        width: 17px;
                        height: 17px;
                        padding-left: 2px;
                        padding-bottom: 0;
                        margin-right: 5px;
                        // vertical-align: bottom;
                        color: transparent;
                        transition: .2s;
                        line-height: 17px;
                    }
                      
                    input[type=checkbox] + label:active:before {
                        transform: scale(0);
                    }
                      
                    input[type=checkbox]:checked + label:before {
                        background-color: var(--color-primary);
                        border-color: var(--color-primary);
                        color: #fff;
                    }
                }
                .submit-btn {
                    margin-top: 7px;
                }
            }
        }
    }
}
