/*============================ 
// Global styles
=============================*/
html {
  overflow-x: hidden !important;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

#main_content {
  background-color: #fff;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: var(--color-primary);
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--color-primary);
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

.section-padding {
  padding-block: 140px;
}
@media (max-width: 1199px) {
  .section-padding {
    padding: 80px 0;
  }
}
@media (max-width: 991px) {
  .section-padding {
    padding: 70px 0;
  }
}
@media (max-width: 767px) {
  .section-padding {
    padding: 50px 0;
  }
}

section,
.section {
  position: relative;
}

@media (min-width: 1339px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl {
    max-width: 1315px;
  }
}
