}

.mb--40 {
  margin-bottom: 40px !important;
}

.mt--24 {
  margin-top: 24px !important;
}

.mt--18 {
  margin-top: 18px !important;
}

.mt--60 {
  margin-top: 60px !important;
}

/*=======================================================================
    Button
=========================================================================*/
.sasxo-button .sasxo-btn {
  -webkit-appearance: none;
  cursor: pointer;
  vertical-align: top;
  font-size: 15px;
  font-weight: 700;
  border-radius: 30px;
  white-space: nowrap;
  height: 50px;
  max-width: 100%;
  border-width: 2px !important;
  border-style: solid;
  padding: 12px 30px;
  animation: animation_back_down 0.3s ease forwards;
  position: relative;
  color: var(--color-white);
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  align-items: center;
  display: inline-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
  flex-direction: row-reverse;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.sasxo-button .sasxo-btn:hover {
  color: var(--color-white);
  background-color: var(--color-black);
  border-color: var(--color-black);
  animation: animation_jump_up 0.3s ease forwards;
}
.sasxo-button .sasxo-btn:hover .icon {
  left: 4px;
  margin-left: 9px;
  margin-right: -14px;
  opacity: 1;
}
.sasxo-button .sasxo-btn:hover .button-text {
  margin-left: -13px;
}
.sasxo-button .sasxo-btn .icon {
  position: relative;
  left: -18px;
  margin-right: -18px;
  margin-left: 0;
  opacity: 0;
  transition: all ease 0.3s;
  float: none;
  margin-top: 0;
  font-size: 18px;
  height: 19px;
  line-height: 19px;
  width: 19px;
  text-align: left;
  display: inline-block;
  vertical-align: bottom;
  white-space: normal;
  text-decoration: inherit;
}
.sasxo-button .sasxo-btn .button-text {
  transition: all ease 0.3s;
  display: inline-block;
  vertical-align: bottom;
  white-space: normal;
  text-decoration: inherit;
}
.sasxo-button .sasxo-btn .button-text .title {
  color: var(--color-white);
}

.ayo-button-2 .ayo-btn-2 {
  background: var(--color-primary);
  color: var(--color-white);
  transition: 0.4s;
  font-weight: 700;
  font-size: 15px;
  border-radius: 15px;
  padding: 13px 30px;
  margin: 10px;
  border: 0;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  position: relative;
  z-index: 1;
}
.ayo-button-2 .ayo-btn-2:hover::before {
  visibility: visible;
  opacity: 1;
  left: 0%;
  transition: width 0.4s ease;
  width: 100%;
}
.ayo-button-2 .ayo-btn-2::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 100%;
  width: 0%;
  height: 100%;
  background: #03319E;
  z-index: -1;
  visibility: hidden;
  opacity: 0;
  transition: all 0.4s;
}

.ayo-button-3 {
  overflow: hidden;
}
.ayo-button-3 .ayo-btn-3 {
  position: relative;
  min-width: 140px;
  height: 50px;
  border-radius: 15px;
  border-width: 2px;
  border-style: solid;
  overflow: hidden;
  font-size: 15px;
  font-weight: 700;
  transition: all 0.35s;
  padding: 13px 30px;
  border-color: var(--color-primary);
  font-size: 15px;
  line-height: 19px;
  color: var(--color-white);
}
.ayo-button-3 .ayo-btn-3:hover {
  color: var(--color-primary);
}
.ayo-button-3 .ayo-btn-3:hover::before {
  height: 0;
}
.ayo-button-3 .ayo-btn-3::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  height: 300%;
  border-radius: 50% 50% 0 0/75% 75% 0 0;
  transition: height 0.35s;
  background: var(--color-primary);
}

.ayo-button-4 .ayo-btn-4 {
  border-radius: 15px;
  color: var(--color-white);
  font-family: var(--font-body);
  background-color: var(--color-primary);
  text-align: center;
  text-transform: capitalize;
  display: inline-block;
  line-height: 50px;
  height: 50px;
  padding: 0px 30px;
  position: relative;
  overflow: hidden;
  transition: 0.4s all ease;
}
.ayo-button-4 .ayo-btn-4:hover span {
  top: -50px;
}
.ayo-button-4 .ayo-btn-4:hover span::after {
  opacity: 1;
}
.ayo-button-4 .ayo-btn-4 span {
  position: relative;
  top: 0;
  transition: 0.4s all ease;
  z-index: 1;
}
.ayo-button-4 .ayo-btn-4 span::after {
  content: attr(title);
  display: block;
  opacity: 0;
  transition: 0.4s all ease;
  color: #fff;
  z-index: -1;
}

.ayo-button-5 .ayo-btn-5 {
  border-radius: 15px;
  color: var(--color-white);
  font-family: var(--font-body);
  background-color: var(--color-primary);
  text-align: center;
  text-transform: capitalize;
  display: inline-block;
  line-height: 50px;
  height: 50px;
  padding: 0px 30px;
  position: relative;
  overflow: hidden;
  transition: all 0.35s ease-in-out;
  z-index: 1;
}
.ayo-button-5 .ayo-btn-5:hover::after {
  transform: translateY(-45%) skew(25deg) scale(1.1);
}
.ayo-button-5 .ayo-btn-5::after {
  position: absolute;
  top: inherit;
  right: inherit;
  bottom: inherit;
  left: -10%;
  content: "";
  height: 160px;
  width: 120%;
  z-index: -1;
  background-color: #F84436;
  transition: all 0.3s ease-in-out;
  transform: translateY(-45%) skew(25deg) scale(0);
  z-index: -1;
}

.ayo-button-6 .ayo-btn-6 {
  border-radius: 15px;
  color: var(--color-white);
  font-family: var(--font-body);
  background-color: var(--color-primary);
  text-align: center;
  text-transform: capitalize;
  display: inline-block;
  line-height: 50px;
  padding: 0px 30px;
  height: 50px;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.ayo-button-6 .ayo-btn-6:hover::before {
  transform: scale(1, 1);
  transform-origin: bottom left;
}
.ayo-button-6 .ayo-btn-6::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0%;
  z-index: -1;
  background-color: #573ef1;
  transform: scale(1, 0);
  transform-origin: top right;
  transition: transform 500ms cubic-bezier(0.86, 0, 0.07, 1);
  transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
  border-radius: 15px;
}

.ayo-button-7 .ayo-btn-7 {
  border-radius: 15px;
  color: var(--color-white);
  font-family: var(--font-body);
  background-color: var(--color-primary);
  text-align: center;
  text-transform: capitalize;
  display: inline-block;
  line-height: 50px;
  padding: 0px 30px;
  height: 50px;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.ayo-button-7 .ayo-btn-7:hover::before {
  left: 0;
  right: inherit;
  width: 100%;
}
.ayo-button-7 .ayo-btn-7::before {
  position: absolute;
  content: "";
  left: inherit;
  right: 0;
  top: 0;
  z-index: -1;
  height: 100%;
  width: 0;
  border-radius: 15px;
  background: #D40F00;
  transition: all 240ms ease-in-out;
}

.ayo-button-8 .ayo-btn-8 {
  border-radius: 15px;
  color: var(--color-white);
  font-family: var(--font-body);
  background-color: var(--color-primary);
  text-align: center;
  text-transform: capitalize;
  display: inline-block;
  line-height: 50px;
  padding: 0px 30px;
  height: 50px;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.ayo-button-8 .ayo-btn-8:hover::before {
  transform: skewX(-45deg) scale(1, 1);
}
.ayo-button-8 .ayo-btn-8::before {
  position: absolute;
  content: "";
  left: -20%;
  right: -20%;
  top: 0;
  bottom: 0;
  transform: skewX(-45deg) scale(0, 1);
  transition: all 0.3s ease;
  background: #f97903;
  z-index: -1;
}

@keyframes animation_back_down {
  0% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes animation_jump_up {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-3px);
  }
}
